import { ChangeEvent, useCallback, useEffect, useState } from "react";
import BaseModal from "../../components/BaseModal";
import LoadingModal from "../LoadingModal";

import styles from "./index.module.scss";
import OperationResultModal from "../OperationResultModal";

interface Props {
  visible: boolean;
  onClose: () => void;
}

const PayPasswordModal: React.FC<Props> = ({ visible, onClose }) => {
  const [passwordValue, setPasswordValue] = useState("");

  const [loadingModalVisible, setLoadingModalVisible] = useState(false);

  const [operationResultModalVisible, setOperationResultModalVisible] =
    useState(false);

  const handlePasswordChange = (event: ChangeEvent<HTMLInputElement>) => {
    const value = event.target.value;

    setPasswordValue(value);
  };

  const submitPayment = useCallback(
    (password: string) => {
      console.log("submitPayment ---", password);

      onClose();

      setLoadingModalVisible(true);

      setTimeout(() => {
        console.log("submitPayment --- ");

        setLoadingModalVisible(false);

        setOperationResultModalVisible(true);
      }, 1500);
    },
    [onClose]
  );

  useEffect(() => {
    passwordValue.length === 6 && submitPayment(passwordValue);
  }, [passwordValue, submitPayment]);

  useEffect(() => {
    !visible && setPasswordValue("");
  }, [visible]);

  return (
    <>
      <BaseModal visible={visible}>
        <div className={styles.wrap}>
          <span className={styles.close} onClick={onClose}></span>
          <div className={styles.title}></div>
          <div className={styles.passwordInput}>
            <input
              type="text"
              maxLength={6}
              onChange={(e) => handlePasswordChange(e)}
            />
            {Array(6)
              .fill(1)
              .map((_, key) => (
                <span key={key} className={styles.box}>
                  <span
                    onLoad={() => console.log("onLoad")}
                    className={`${styles.dot} ${
                      passwordValue.length >= key + 1 ? styles.on : ""
                    }`}
                  ></span>
                </span>
              ))}
          </div>
          <div className={styles.tips}> Slot 6</div>
        </div>
      </BaseModal>

      <OperationResultModal
        visible={operationResultModalVisible}
        onClose={() => setOperationResultModalVisible(false)}
      />

      <LoadingModal
        visible={loadingModalVisible}
        title=""
        description=""
      />
    </>
  );
};

export default PayPasswordModal;
