import { ReactNode, useEffect, useState } from "react";
import { CSSTransition } from "react-transition-group";
import Portal from "../Portal";

import styles from "./index.module.scss";

interface Props {
  visible: boolean;
  children: ReactNode;
}

const getClassNames = (type: "mask" | "inner") => ({
  enter: styles[`${type}Enter`],
  enterActive: styles[`${type}EnterActive`],
  enterDone: styles[`${type}EnterDone`],

  exit: styles[`${type}Exit`],
  exitActive: styles[`${type}ExitActive`],
  exitDone: styles[`${type}ExitDone`],

  appear: styles[`${type}Appear`],
  appearActive: styles[`${type}AppearActive`],
  appearDone: styles[`${type}AppearDone`],
});

const BaseModal: React.FC<Props> = ({ visible, children }) => {
  const [protalVisible, setProtalVisible] = useState(false);

  useEffect(() => {
    if (visible) {
      setProtalVisible(true);
    } else {
    }
  }, [visible]);

  return protalVisible ? (
    <Portal>
      <div className={styles.wrap}>
        <CSSTransition
          appear
          in={visible}
          timeout={300}
          classNames={getClassNames("mask")}
          onExited={() => setProtalVisible(false)}
        >
          <div className={styles.mask}></div>
        </CSSTransition>

        <CSSTransition
          appear
          in={visible}
          timeout={300}
          classNames={getClassNames("inner")}
        >
          <div className={styles.inner}>{children}</div>
        </CSSTransition>
      </div>
    </Portal>
  ) : null;
};

export default BaseModal;
