import styles from "./index.module.scss";

import rewardImg from "../../assets/upload/reward1.png";
import BaseModal from "../../components/BaseModal";
import { useCallback, useEffect, useState } from "react";
import PayPasswordModal from "../PayPasswordModal";

interface Props {
  visible: boolean;
  onClose: () => void;
}

const MOCK_ERROR_MESSAGE = [
  "USDT  USDT",
  " Slot ",
];

const ConfirmOrderModal: React.FC<Props> = ({ visible, onClose }) => {
  const [errorList, setErrorList] = useState<string[]>([]);

  const [payPasswordModalVisible, setPayPasswordModalVisible] = useState(false);

  const handleClosePayPasswordModal = useCallback(() => {
    setPayPasswordModalVisible(false);
  }, []);

  useEffect(() => {
    !visible && setErrorList([]);
  }, [visible]);

  return (
    <>
      <BaseModal visible={visible}>
        <div className={styles.wrap}>
          <span className={styles.close} onClick={onClose}></span>
          <div className={styles.title}></div>
          <div className={styles.orderInfo}>
            <div className={styles.pic}>
              <img src={rewardImg} alt="product" />
            </div>
            <div className={styles.list}>
              <div className={styles.baseInfo}>
                <span className={styles.type}></span>
                <span className={styles.productLabel}>LV.A-#10000</span>
              </div>
              <div className={styles.totalPrice}>
                <span className={styles.type}></span>
                <span className={styles.price}>
                  <span className={styles.num}>18889.11</span>
                  <span className={styles.currency}>USDT</span>
                </span>
              </div>
              <div className={styles.handlingFee}>
                <span className={styles.type}></span>
                <span className={styles.price}>
                  <span className={styles.num}>188</span>
                  <span className={styles.currency}></span>
                </span>
              </div>
            </div>
          </div>
          {!!errorList.length && (
            <div className={styles.errorMessage}>
              <span>USDT  USDT</span>
              <span> Slot </span>
            </div>
          )}

          <div className={styles.btns}>
            <span
              className={styles.payBtn}
              onClick={() => setErrorList(MOCK_ERROR_MESSAGE)}
            >
              
            </span>
            <span
              className={styles.payBtn}
              onClick={() => {
                onClose();
                setPayPasswordModalVisible(true);
              }}
            >
              
            </span>
          </div>
        </div>
      </BaseModal>

      <PayPasswordModal
        visible={payPasswordModalVisible}
        onClose={handleClosePayPasswordModal}
      />
    </>
  );
};

export default ConfirmOrderModal;
