import { useCallback, useState } from "react";
import EmptyCart from "./EmptyCart";
import LoadingModal from "../LoadingModal";
import PayPasswordModal from "../PayPasswordModal";
import { CSSTransition } from "react-transition-group";

import styles from "./index.module.scss";
import rewardImg1 from "../../assets/upload/reward1.png";
import { useNavigate } from "react-router-dom";

const MOCK_PRODUCT_COUNT = 8;

const ShoppingCart = () => {
  const navigate = useNavigate();

  const [editing, setEditing] = useState(false);

  const [checkedAll, setCheckedAll] = useState(false);

  const [checkedList, setCheckedList] = useState<number[]>([]);

  const [payPasswordModalVisible, setPayPasswordModalVisible] = useState(false);

  // 
  const handleToggleCheckedAll = () => {
    setCheckedAll(!checkedAll);

    setCheckedList(
      checkedAll
        ? []
        : Array(MOCK_PRODUCT_COUNT)
            .fill(1)
            .map((_, key) => key)
    );
  };

  // 
  const handleToggleChecked = (
    event:
      | React.MouseEvent<HTMLSpanElement>
      | React.TouchEvent<HTMLSpanElement>,
    id: number
  ) => {
    event.stopPropagation();

    const list = checkedList.includes(id)
      ? checkedList.filter((value) => value !== id)
      : [...checkedList, id];

    setCheckedList(list);
  };

  // 
  const handleToggleEditingMode = () => {
    setEditing(!editing);

    setCheckedAll(false);

    setCheckedList([]);
  };

  // 
  const handleDelete = (
    event:
      | React.MouseEvent<HTMLSpanElement>
      | React.TouchEvent<HTMLSpanElement>,
    id: number
  ) => {
    event.stopPropagation();

    console.log(" id: ", id);
  };

  const handleClosePayPasswordModal = useCallback(() => {
    setPayPasswordModalVisible(false);
  }, []);

  return (
    <>
      <div className={styles.wrap}>
        <div className={styles.main}>
          {MOCK_PRODUCT_COUNT ? (
            <ul>
              {Array(MOCK_PRODUCT_COUNT)
                .fill(1)
                .map((_, key) => (
                  <li
                    key={key}
                    onClick={() => navigate(`/productDetails/${key}`)}
                  >
                    <span
                      className={styles.col1}
                      onClick={(e) => handleToggleChecked(e, key)}
                    >
                      <span
                        className={`${styles.checkBox} ${
                          checkedList.includes(key) ? styles.on : ""
                        }`}
                      ></span>
                    </span>
                    <span className={styles.col2}>
                      <div className={styles.pic}>
                        <img src={rewardImg1} alt="product" />
                      </div>
                    </span>
                    <span className={styles.col3}>
                      <div className={styles.category}></div>
                      <div className={styles.productLabel}>Lv.A-#10000</div>
                      <div className={styles.price}>
                        <span className={styles.num}>15550.11</span>
                        <span className={styles.currency}>USDT</span>
                      </div>
                    </span>
                    <CSSTransition
                      appear
                      in={editing}
                      timeout={300}
                      classNames={{
                        appear: styles.appear,
                        appearActive: styles.appearActive,
                        appearDone: styles.appearDone,
                        enter: styles.enter,
                        enterActive: styles.enterActive,
                        enterDone: styles.enterDone,
                        exit: styles.exit,
                        exitActive: styles.exitActive,
                        exitDone: styles.exitDone,
                      }}
                    >
                      <span
                        className={styles.col4}
                        onClick={(e) => handleDelete(e, key)}
                      >
                        <span className={styles.deleteBtn}></span>
                      </span>
                    </CSSTransition>
                  </li>
                ))}
            </ul>
          ) : (
            <EmptyCart />
          )}
        </div>
        <div className={`${styles.footer}`}>
          {MOCK_PRODUCT_COUNT ? (
            <div className={styles.settlementBar}>
              <span
                className={styles.checkedAll}
                onClick={handleToggleCheckedAll}
              >
                <span
                  className={`${styles.checkBox} ${
                    checkedAll ? styles.on : ""
                  }`}
                ></span>
                <span className={styles.txt}></span>
              </span>
              {checkedAll ? (
                <div className={styles.checkoutList}>
                  <div className={styles.totalPrice}>
                    <span className={styles.type}></span>
                    <span className={styles.price}>
                      <span className={styles.num}>18880.88</span>
                      <span className={styles.currency}>USDT</span>
                    </span>
                  </div>
                  <div className={styles.handlingFee}>
                    <span className={styles.type}></span>
                    <span className={styles.price}>
                      <span className={styles.num}>20889</span>
                      <span className={styles.currency}></span>
                    </span>
                  </div>
                </div>
              ) : null}
            </div>
          ) : null}
          {false ? (
            <div className={styles.errorMessage}>
              <span>USDT  USDT</span>
              <span> Slot </span>
              <span>USDT </span>
            </div>
          ) : null}

          <div className={styles.actions}>
            {!editing ? (
              <>
                <span
                  className={MOCK_PRODUCT_COUNT ? "" : styles.disable}
                  onClick={handleToggleEditingMode}
                >
                  
                </span>
                <span
                  className={MOCK_PRODUCT_COUNT ? styles.on : styles.disable}
                  onClick={() => setPayPasswordModalVisible(true)}
                >
                  
                </span>
              </>
            ) : (
              <>
                <span className={checkedList.length ? "" : styles.disable}>
                  
                </span>
                <span className={styles.on} onClick={handleToggleEditingMode}>
                  
                </span>
              </>
            )}
          </div>
        </div>
      </div>

      <PayPasswordModal
        visible={payPasswordModalVisible}
        onClose={handleClosePayPasswordModal}
      />
    </>
  );
};

export default ShoppingCart;
