import styles from "./index.module.scss";

import bannerImg from "../../assets/upload/banner.png";
import BaseModal from "../../components/BaseModal";
import React, { useState } from "react";

interface Props {
  visible: boolean;
  onClose: () => void;
}

const GuideModal: React.FC<Props> = ({ visible, onClose }) => {
  const [checked, setChecked] = useState(false);

  console.log("checked --- ", checked);

  return (
    <BaseModal visible={visible}>
      <div className={styles.wrap}>
        <span className={styles.close} onClick={onClose}></span>
        <div className={styles.title}></div>
        <div className={styles.banner}>
          <img src={bannerImg} alt="banner" />
        </div>
        <div className={styles.desc}>
           Slot NFT  NFT 
        </div>
        <div className={styles.btns}>
          <span className={styles.buyBtn}> NFT</span>
          <span className={styles.sellBtn}> NFT</span>
        </div>
        <div className={styles.controlBox} onClick={() => setChecked(!checked)}>
          <span
            className={`${styles.checkBox} ${checked ? styles.on : ""}`}
          ></span>
          <span className={styles.txt}></span>
        </div>
      </div>
    </BaseModal>
  );
};

export default GuideModal;
