import { CSSTransition } from "react-transition-group";

import styles from "./index.module.scss";
import { useEffect, useState } from "react";

interface Props {
  visible: boolean;
  onClose: () => void;
}

const FilterPanel: React.FC<Props> = ({ visible, onClose }) => {
  const [selectListVisible, setSelectListVisible] = useState(false);

  const [transitionVisible, setTransitionVisible] = useState(false);

  useEffect(() => {
    visible && setTransitionVisible(true);
  }, [visible]);

  return transitionVisible ? (
    <>
      <CSSTransition
        appear
        in={visible}
        timeout={300}
        classNames={{
          appear: styles.maskAppear,
          appearActive: styles.maskAppearActive,
          appearDone: styles.maskAppearDone,
          exit: styles.maskExit,
          exitActive: styles.maskExitActive,
          exitDone: styles.maskExitDone,
        }}
      >
        <div className={styles.mask}></div>
      </CSSTransition>
      <CSSTransition
        appear
        in={visible}
        timeout={300}
        classNames={{
          appear: styles.panelAppear,
          appearActive: styles.panelAppearActive,
          appearDone: styles.panelAppearDone,
          exit: styles.panelExit,
          exitActive: styles.panelExitActive,
          exitDone: styles.panelExitDone,
        }}
        onExited={() => setTransitionVisible(false)}
      >
        <div className={styles.wrap}>
          <div className={styles.title}></div>
          <span className={styles.close} onClick={onClose}></span>
          <div className={styles.main}>
            <div className={styles.filterItem}>
              <h3></h3>
              <ul>
                <li></li>
                <li></li>
                <li className={styles.placeholder}></li>
              </ul>
            </div>
            <div className={styles.filterItem}>
              <h3></h3>
              <ul>
                <li className={styles.on}></li>
                <li></li>
                <li></li>
              </ul>
            </div>

            <div className={styles.filterItem}>
              <h3>Slot</h3>
              <ul>
                {[
                  "",
                  "",
                  "",
                  "",
                  "",
                  "",
                  "",
                  "",
                  "",
                ].map((theme, key) => (
                  <li key={key}>{theme}</li>
                ))}
              </ul>
            </div>
            <div className={styles.filterItem}>
              <h3></h3>
              <ul>
                <li>Lv.A</li>
                <li>Lv.B</li>
                <li>Lv.C</li>
                <li>Lv.D</li>
              </ul>
            </div>
            <div className={styles.filterItem}>
              <h3></h3>
              <div className={styles.select}>
                <div
                  className={styles.selected}
                  onClick={() => setSelectListVisible(!selectListVisible)}
                >
                  <span className={styles.txt}></span>
                  <i className={styles.arrow}></i>
                </div>
                <div
                  className={`${styles.selectList} ${
                    selectListVisible ? styles.on : ""
                  }`}
                >
                  <p className={styles.on}></p>
                  <p></p>
                  <p></p>
                </div>
              </div>
            </div>

            <div className={styles.btns}>
              <span className={styles.btn}></span>
            </div>
          </div>
        </div>
      </CSSTransition>
    </>
  ) : null;
};

export default FilterPanel;
