import { useLocation, useNavigate } from "react-router-dom";

import styles from "./index.module.scss";

import logoImg from "../../assets/logo.png";

const Header = () => {
  const navigate = useNavigate();

  const { pathname } = useLocation();

  return pathname.includes("shoppingCart") ? (
    <div className={styles.shoppingCartWrap}>
      <div className={styles.title}></div>
      <span className={styles.close} onClick={() => navigate(-1)}></span>
    </div>
  ) : (
    <div className={styles.wrap}>
      <span className={styles.logo} onClick={() => navigate("/market")}>
        <img src={logoImg} alt="logo" />
      </span>
      {pathname.includes("menuSideBar") ? (
        <span className={styles.close} onClick={() => navigate(-1)} />
      ) : (
        <span
          className={styles.menu}
          onClick={() => navigate("/menuSideBar")}
        />
      )}
    </div>
  );
};

export default Header;
